
var kaObj = JSON.parse(sessionStorage.kaObj)
var userName = localStorage.userName
var passWord = localStorage.passWord

import Group from 'vux/src/components/group/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import { mapState , mapGetters, mapMutations } from 'vuex'
export default {
name:'information',
components:{
Group,
Selector,
Datetime,
XTextarea,
Flexbox,
FlexboxItem,
XButton,
Cell,
XTextarea
},
data(){
return{
dateValue:'',    //时间日期选择
titD:"送货日期选择"+`<span style="color:red">*</span>`,   //添加*为必填选项
textValue:''   
}
},
computed:{


},
watch:{
},
methods:{
...mapGetters(['msgTj']),
...mapMutations(['cancelChooseList']),
tj(){
if(!this.dateValue){
this.$vux.alert.show({
title: '注意',
content: '请选择送货日期',
})
}else{
const _this = this
this.$vux.confirm.show({
title:"注意",
content:"确定要提交吗？",
onCancel () {      //取消的回调

},
onConfirm () {    //确定的回调
_this.msgTj()
console.log(_this.tjProductList)
_this.$axios.get(_this.url+'preorderKaController.do?saveKaddcx',{
params:{
userName:userName,
passWord:passWord,
customerCode :kaObj.customerCode,
storescode:kaObj.storescode,




products:JSON.stringify(_this.tjProductList) ,  //产品
dbillDate:_this.dateValue,  //日期
vnote:_this.textValue     
}
}).then(res=>{
console.log(res)
if(res.data.success == true){
_this.cancelChooseList()      
_this.textValue =""
_this.$vux.toast.show({
time:'1500',
type:'success',
text: res.data.msg
})
setTimeout(() => {
_this.$router.push({path:"/"})
}, 1500);

}else{
_this.$vux.toast.show({
time:'1500',
type:'cancel',
text: res.data.msg
})
}
}).catch(e=>{
_this.$vux.alert.show({
title: '注意',
content: '服务器出错,请稍后再试',
})
})
}
})

}
}

},
computed:{
...mapState(['tjProductList'])
},


















}



